import { useCallback, useEffect, useRef, useState } from "react";

export const useHttpClient = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState();

  const activeHttpRequesets = useRef([]);

  const sendRequest = useCallback(
    async (url, method = "GET", body = null, headers = {}) => {
      setIsLoading(true);
      const htteoAbortCtrll = new AbortController();
      activeHttpRequesets.current.push(htteoAbortCtrll);

      try {
        const res = await fetch(url, {
          method,
          body,
          headers,
          signal: htteoAbortCtrll.signal,
        });

        const data = await res.json();

        activeHttpRequesets.current = activeHttpRequesets.current.filter(
          (reqCtrl) => reqCtrl !== htteoAbortCtrll
        );

        if (!res.ok) {
          throw new Error(data.message);
        }

        setIsLoading(false);

        return data;
      } catch (err) {
        setError(err.message);
        setIsLoading(false);
        throw err;
      }
    },
    []
  );

  const clearError = () => {
    setError(null);
  };

  useEffect(() => {
    return () => {
      activeHttpRequesets.current.forEach((abortCtrl) => abortCtrl.abort());
    };
  }, []);

  return { isLoading, error, sendRequest, clearError };
};
